h1{
  font-size: 1.8rem; 
  padding:1rem 0;
}

h2{
  font-size: 1.4rem;
  padding:0.5rem 0;
}

h3{
  padding:1rem 0;
}

.rating span{
margin:0.1rem;
}

/* carousel */
.carousel-item-next,
.carousel-item-prev,
.carousel-item.active {
  display: flex;
}
.carousel-caption {
  position: absolute;
  top: 0;
}

.carousel-caption h4 {
  color: #fff;
}

.carousel img {
  display:block;
  height: 300px;
  padding: 30px;
  margin: 40px;
  border-radius: 50%;
  margin-left: auto;
  margin-right: auto;
}
.carousel a {
  margin: 0 auto;
}
@media (max-width: 900px) {
  .carousel-caption h2 {
    font-size: 2.5vw;
  }
}


footer {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  min-height: 100vh;
  flex-direction: column;
  background: #343a40;
  font-family: "Poppins", sans-serif;
}

footer {
  position: relative;
  width: 100%;
  height: auto;
  padding: 50px 100px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

footer .container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

footer .container .sec {
  margin-right: 30px;
}

footer .container .sec.about-us {
  width: 40%;
}

footer .container h2 {
  position: relative;
  color: #fff;
  font-weight: 500;
  margin-bottom: 15px;
}

footer .container h2::before {
  content: "";
  position: absolute;
  bottom: -5px;
  left: 0;
  width: 50px;
  height: 2px;
  background: #f00;
}
footer p {
  color: #999;
}
.sci {
  margin-top: 20px;
  display: flex;
}

.sci li {
  list-style: none;
}

.sci li a {
  display: inline-block;
  width: 40px;
  height: 40px;
  /* background: #222; */
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 10px;
  text-decoration: none;
  border-radius: 4px;
  color: #fff;
}
.sci li a:hover {
  background: #f00;
}
.sci li a .fa {
  color: #fff;
  font-size: 20px;
}

.quick-links {
  position: relative;
  width: 25%;
}
.quick-links ul li {
  list-style: none;
}

.quick-links ul li a {
  color: #999;
  text-decoration: none;
  margin-bottom: 10px;
  display: inline-block;
}
.quick-links ul li a:hover {
  color: #fff;
}
.contact {
  width: calc(35%-60px);
  margin-right: 0 !important;
}

.contact .info {
  position: relative;
}

.contact .info li {
  display: flex;
  margin-bottom: 16px;
}

.contact .info li span:nth-child(1) {
  color: #fff;
  font-size: 20px;

  margin-right: 10px;
}
.contact .info li span {
  color: #999;
}

.contact .info li a {
  color: #999;

  text-decoration: none;
}

.contact .info li a:hover {
  color: #fff;
}

.copyright {
  color: #999;
  width: 100%;
  background-color: #444444;
}

@media (max-width: 991px) {
  footer {
    padding: 40px;
  }
  footer .container {
    flex-direction: column;
  }
  footer .container .sec {
    margin-right: 0;
    margin-bottom: 40px;
  }
  footer .container .sec.about-us,
  .quick-links,
  .contact {
    width: 100%;
  }
  footer .copyright {
    padding: 8px 0px;
  }
}

.container{
    margin-top:5%;
  }

.sup {
  position: relative;
  bottom: 1ex; 
  font-size: 90%;
  /* background-color:red;
  border-radius: 50% */
}




.cat-wrapper {
  height: 100%;
  position: fixed;
  background-color: rgba(0, 0, 0, 0.822);
  display: flex;
  flex-direction: column;
  width: 200px;
  z-index: 10000;
  visibility: hidden;
  padding: 50px 0px 40px 0;
  color: gray;
}
.cat-wrapper.active {
  visibility: visible;
  
}
.cat-list {
  display: flex;
  flex-direction: column;
}
.cat-heading {
  font-size: 1.4rem;
}
a.cat-link {
  text-decoration: none;
}

.cat-link-wrapper {
  background-color: rgb(214, 231, 231);
  padding: 10px;
  transition: linear 0.2s;
}

.cat-link-wrapper:hover {
  background-color: aliceblue;
  padding: 20px;
}
.fas.hamburger {
  font-size: 2rem;
  color: aliceblue;
  transition: linear 0.2s;
}

